<template>
  <v-card
    height="100%"
  >
    <div
      id="nodocentes-prestaciones"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Prestaciones por Categoría.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('nodocentes-prestaciones')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as No Docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Categoría
                </th>
                <th class="text-left">
                  Prestaciones
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in categoriaData"
                :key="index"
              >
                <td>{{ parseCategoria(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['nodocentesPrestaciones', 'mobileSize']),
    barChartOptions () {
      return {
        chartArea: {
          left: 150,
          right: 20,
          top: 20,
          bottom: 20
        },
        // tooltip: {
        //   isHtml: true,
        //   textStyle: { color: 'white', bold: false }
        // },
        colors: this.colors,
        title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        legend: {
          position: 'none',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '70%' },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        hAxis: {
          format: '0',
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    barChartData () {
      return {
        cols: [
          { id: 'Categoria', label: 'Prestaciones', type: 'string' },
          { id: 'Categoria', label: 'Prestaciones', type: 'number' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'Prest. Serv. Sec LyT' },
              { v: this.categoriaData.plyt, f: this.categoriaData.plyt }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest Serv Com Mem' },
              { v: this.categoriaData.pmem, f: this.categoriaData.pmem }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest. Serv. Genero' },
              { v: this.categoriaData.pgen, f: this.categoriaData.pgen }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest. Serv. Deporte' },
              { v: this.categoriaData.pdep, f: this.categoriaData.pdep }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest. Serv. Multi' },
              { v: this.categoriaData.pmul, f: this.categoriaData.pmul }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest de Serv Medios' },
              { v: this.categoriaData.pmed, f: this.categoriaData.pmed }
            ]
          },
          {
            c: [
              { v: 0, f: 'Prest. Serv. Cult' },
              { v: this.categoriaData.pcul, f: this.categoriaData.pcul }
            ]
          },
          {
            c: [
              { v: 0, f: 'Contr.Pres.Serv ASJU' },
              { v: this.categoriaData.casju, f: this.categoriaData.casju }
            ]
          },
          {
            c: [
              { v: 0, f: 'Cont. Proy. Esp.' },
              { v: this.categoriaData.cesp, f: this.categoriaData.cesp }
            ]
          },
          {
            c: [
              { v: 0, f: 'Contr Prest Serv Med' },
              { v: this.categoriaData.cmed, f: this.categoriaData.cmed }
            ]
          },
          {
            c: [
              { v: 0, f: 'CHOFER' },
              { v: this.categoriaData.cho, f: this.categoriaData.cho }
            ]
          }
        ]
      }
    },
    categoriaData () {
      // "Prest. Serv. Sec LyT"
      // "Prest Serv Com Mem"
      // "Prest. Serv. Genero"
      // "Prest Serv Dep"
      // "Prest. Serv. Deporte"
      // "Prest. Serv. Dep"
      // "Prest. Serv. Multi"
      // "Prest de Serv Medios"
      // "Prest. Serv. Cult"
      // "Contr.Pres.Serv ASJU"
      // "Cont. Proy. Esp."
      // "Contr Prest Serv Med"
      // "CHOFER"
      return this.nodocentesPrestaciones
        .reduce(
          (acc, cur) => {
            const pselyt = cur.descripcion_categoria === 'Prest. Serv. Sec LyT'
            const psemem = cur.descripcion_categoria === 'Prest Serv Com Mem'
            const psegen = cur.descripcion_categoria === 'Prest. Serv. Genero'
            const psedep = cur.descripcion_categoria === 'Prest Serv Dep' || cur.descripcion_categoria === 'Prest. Serv. Deporte' || cur.descripcion_categoria === 'Prest. Serv. Dep'
            const psemul = cur.descripcion_categoria === 'Prest. Serv. Multi'
            const psemed = cur.descripcion_categoria === 'Prest de Serv Medios'
            const psecul = cur.descripcion_categoria === 'Prest. Serv. Cult'
            const contasju = cur.descripcion_categoria === 'Contr.Pres.Serv ASJU'
            const contesp = cur.descripcion_categoria === 'Cont. Proy. Esp.'
            const conmed = cur.descripcion_categoria === 'Contr Prest Serv Med'
            const chofer = cur.descripcion_categoria === 'CHOFER'
            return pselyt
              ? { ...acc, plyt: acc.plyt + 1 }
              : psemem
                ? { ...acc, pmem: acc.pmem + 1 }
                : psegen
                  ? { ...acc, pgen: acc.pgen + 1 }
                  : psedep
                    ? { ...acc, pdep: acc.pdep + 1 }
                    : psemul
                      ? { ...acc, pmul: acc.pmul + 1 }
                      : psemed
                        ? { ...acc, pmed: acc.pmed + 1 }
                        : psecul
                          ? { ...acc, pcul: acc.pcul + 1 }
                          : contasju
                            ? { ...acc, casju: acc.casju + 1 }
                            : contesp
                              ? { ...acc, cesp: acc.cesp + 1 }
                              : conmed
                                ? { ...acc, cmed: acc.cmed + 1 }
                                : chofer
                                  ? { ...acc, cho: acc.cho + 1 }
                                  : acc
          },
          {
            plyt: 0,
            pmem: 0,
            pgen: 0,
            pdep: 0,
            pmul: 0,
            pmed: 0,
            pcul: 0,
            casju: 0,
            cesp: 0,
            cmed: 0,
            cho: 0
          }
        )
    }
  },
  methods: {
    parseCategoria (v) {
      return v === 'plyt'
        ? 'Prest. Serv. Sec LyT'
        : v === 'pmem'
          ? 'Prest Serv Com Mem'
          : v === 'pgen'
            ? 'Prest. Serv. Genero'
            : v === 'pdep'
              ? 'Prest. Serv. Deporte'
              : v === 'pmul'
                ? 'Prest. Serv. Multi'
                : v === 'pmed'
                  ? 'Prest de Serv Medios'
                  : v === 'pcul'
                    ? 'Prest. Serv. Cult'
                    : v === 'casju'
                      ? 'Contr.Pres.Serv ASJU'
                      : v === 'cesp'
                        ? 'Cont. Proy. Esp.'
                        : v === 'cmed'
                          ? 'Contr Prest Serv Med'
                          : v === 'cho'
                            ? 'Chofer'
                            : v
    }
  }
}
</script>
